body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
  background: tomato;
}

ol, ul {
  padding-left: 30px;
}


.game {
  width: 100%;
  max-width: 540px;
  height: 700px;
  margin: 50px auto;
}

.game-info {
  margin-left: 20px;
  text-align: center;
  font-size: 35px;
  width: 100%;
}

.board {
  width: 540px;
  height: 540px;
  box-shadow: 2px 2px 10px rgb(0, 0, 0, 0.5);
  display: block;

}


.board-row {


}

.status {
  margin-bottom: 20px;
  text-align: center;
  font-size: 35px;
  width: 100%;

}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 120px;
  font-weight: bold;
  line-height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 30px;
  text-align: center;
  width: 180px;
  height: 180px;

}

.square:focus {
  outline: none;
}

button.rigioca {
  padding: 20px 30px;
  background-color: #FFF;
  display: block;
  margin: 20px auto;
  outline: none;
  border: none;
  border-radius: 10px;
  box-shadow: 2px 2px 10px rgb(0, 0, 0, 0.5);

}

button.rigioca:hover {
  cursor: pointer;
}

button.rigioca:active {
  background-color: #DDD;
}

.kbd-navigation .square:focus {
  background: #ddd;
}


#errors {
  background: #c00;
  color: #fff;
  display: none;
  margin: -20px -20px 20px;
  padding: 20px;
  white-space: pre-wrap;
}
